html {
  --blue-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: block;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  line-height: 60px;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  margin: 0 0 0 -20px;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  height: 60px;
  margin-left: 20px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--blue-color);
}

.nav__link:hover:after {
  width: 100%;
  transition: width .3s;
}
